import { Checkbox, Grid } from "@material-ui/core";
import React, { useEffect, useState } from "react";
import { useForm, Form } from "../../Components/useForm";
import axios from "axios";
import { FormControl, FormControlLabel } from "@mui/material";
import Control from "../../Controls/Control";
import {
  getBloodCollection,
  getDepartmentCollections,
} from "../../Services/employeeServices";

const genderItems = [
  {
    id: "male",
    title: "Male",
  },
  {
    id: "female",
    title: "Female",
  },
  {
    id: "other",
    title: "Other",
  },
];
const initialFieldValues = {
  // id: 0,
  // firstName: "",
  // email: "",
  // phone: "",
  // city: "",
  // gender: "male",
  // departmentId: "",
  // birthday: new Date(),
  // isPermanent: false,
  id: "",
  gender: "male",
  firstName: "",
  lastName: "",
  phone: "",
  mail: "",
  birthday: new Date(),
  bloodId: "",
  streetAddress: "",
  streetAddress2: "",
  city: "",
  state: "",
  country: "",
  zipcode: "",
  genderx: "",
  firstNamex: "",
  lastNamex: "",
  relationx: "",
  phonex: "",
  streetAddressx: "",
  streetAddress2x: "",
  cityx: "",
  statex: "",
  countryx: "",
  zipcodex: "",
};

function EmployeeForm() {
  const API_URL = "http://localhost:8080/employees";

  const validate = (fieldValues = values) => {
    let temp = { ...errors };
    if ("firstName" in fieldValues)
      temp.firstName = fieldValues.firstName ? "" : "This field is required.";
    if ("mail" in fieldValues)
      temp.mail = /$^|.+@.+..+/.test(fieldValues.mail)
        ? ""
        : "Email is not Valid.";
    if ("phone" in fieldValues)
      temp.phone =
        fieldValues.phone.length > 9 ? "" : "This field is required.";
    if ("departmentId" in fieldValues)
      temp.departmentId =
        fieldValues.departmentId.length != 0 ? "" : "This field is required.";
    setErrors({
      ...temp,
    });
    if (fieldValues == values) return Object.values(temp).every((x) => x == "");
  };

  const { values, setvalues, errors, setErrors, resetForm, handleInputChange } =
    useForm(initialFieldValues, true, validate);

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (validate()) window.alert("Testing");
    try {
      const response = await axios.post(API_URL, values);
      console.log(response.data);
    } catch (error) {
      console.error(error);
    }
  };

  return (
    <Form onSubmit={handleSubmit}>
      <Grid container>
        <Grid item xs={6}>
          <Control.Input
            name="firstName"
            label="First Name"
            value={values.firstName}
            onChange={handleInputChange}
            error={errors.firstName}
          />
          <Control.Input
            name="lastName"
            label="Last Name"
            value={values.lastName}
            onChange={handleInputChange}
            error={errors.lastName}
          />

          <Control.Input
            name="phone"
            label="Phone Number"
            value={values.phone}
            onChange={handleInputChange}
            error={errors.phone}
          />
          <Control.Input
            name="mail"
            label="Email"
            value={values.mail}
            onChange={handleInputChange}
            error={errors.mail}
          />
          <Control.DatePicker
            name="birthday"
            label="Birthday"
            value={values.birthday}
            onChange={handleInputChange}
          ></Control.DatePicker>
          <Control.Select
            name="bloodId"
            label="Blood"
            value={values.bloodId}
            onChange={handleInputChange}
            options={getBloodCollection()}
          ></Control.Select>
          <Control.Input
            name="streetAddress"
            label="Street Address"
            value={values.streetAddress}
            onChange={handleInputChange}
            error={errors.streetAddress}
          />
          <Control.Input
            name="streetAddress2"
            label="Street Address Line 2"
            value={values.streetAddress2}
            onChange={handleInputChange}
          />
          <Control.Input
            name="city"
            label="City"
            value={values.city}
            onChange={handleInputChange}
          />
          <Control.Input
            name="state"
            label="State"
            value={values.state}
            onChange={handleInputChange}
          />
          <Control.Input
            name="country"
            label="Country"
            value={values.country}
            onChange={handleInputChange}
          />
          <Control.Input
            name="zipcode"
            label="Zip Code"
            value={values.zipcode}
            onChange={handleInputChange}
            error={errors.zipcode}
          />
        </Grid>

        <Grid item xs={6}>
          <Control.RadioGroup
            name="gender"
            label="Gender"
            value={values.gender}
            onChange={handleInputChange}
            items={genderItems}
          />
          <Control.Select
            name="departmentId"
            label="Department"
            value={values.departmentId}
            onChange={handleInputChange}
            options={getDepartmentCollections()}
            error={errors.departmentId}
          ></Control.Select>

          <FormControlLabel
            required
            control={<Checkbox />}
            label="I agree to the terms and conditions as set out by the user agreement."
          />
          <div>
            <Control.Button type="submit" text="Submit" />
            <Control.Button color="Default" text="Reset" onClick={resetForm} />
          </div>
        </Grid>
      </Grid>
    </Form>
  );
}

export default EmployeeForm;
