import React from "react";
import EmployeeForm from "./EmployeeForm";
import PageHeader from "../../Components/PageHeader";
import PeopleIcon from "@mui/icons-material/People";
import {
  InputAdornment,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Toolbar,
  makeStyles,
} from "@material-ui/core";
import Control from "../../Controls/Control";
import AddIcon from "@mui/icons-material/Add";
import SearchIcon from "@mui/icons-material/Search";
import useTable from "../../Components/useTable";
import axios from "axios";
import { useState } from "react";
import { useEffect } from "react";
import { useForm } from "../../Components/useForm";
import { getBlood, getBloodCollection } from "../../Services/employeeServices";
import GetAllEmployees from "../../Services/getAllEmployee";

const useStyles = makeStyles((theme) => ({
  pageContent: {
    margin: theme.spacing(5),
    padding: theme.spacing(3),
  },
}));

const headCells = [
  { id: "id", label: "ID" },
  { id: "gender", label: "Gender" },
  { id: "fullName", label: "Full Name" },
  { id: "phone", label: "Phone" },
  { id: "mail", label: "Email" },
  { id: "birthday", label: "Birthday" },
  { id: "blood", label: "Blood" },
  { id: "address", label: "Address" },
  { id: "emergency", label: "Emergency Contact Details" },
  { id: "", label: "" },
];

function Employees() {
  const employees = GetAllEmployees();
  const classes = useStyles();

  const { TblContainer, TblHead } = useTable(employees, headCells);

  return (
    <>
      <PageHeader
        title="New Employee"
        subTitle="Form Design With Validation"
        icon={<PeopleIcon fontSize="large" />}
      />
      <Paper elevation={0} className={classes.pageContent}>
        {/* <EmployeeForm /> */}
        <TblContainer>
          <TblHead></TblHead>
          <TableBody>
            {employees.length > 0 ? (
              employees.map((employee) => (
                <TableRow key={employee.id}>
                  <TableCell>{employee.id}</TableCell>
                  <TableCell>{employee.gender}</TableCell>
                  <TableCell>
                    {employee.firstName} {employee.lastName}
                  </TableCell>
                  <TableCell>{employee.phone}</TableCell>
                  <TableCell>{employee.mail}</TableCell>
                  <TableCell>{employee.birthday}</TableCell>
                  <TableCell>{employee.blood}</TableCell>
                  <TableCell>
                    {employee.streetAddress} {employee.streetAddress2}
                    <br />
                    {employee.city} {employee.state}
                    <br />
                    {employee.country} {employee.zipcode}
                  </TableCell>
                  <TableCell>
                    {employee.firstNamex} {employee.lastNamex}
                    <br />
                    {employee.relationx}
                    <br />
                    {employee.phonex}
                    <br />
                    {employee.streetAddressx} {employee.streetAddress2x} <br />
                    {employee.cityx} {employee.statex}
                    <br />
                    {employee.countryx} {employee.zipcodex}
                  </TableCell>
                </TableRow>
              ))
            ) : (
              <TableRow>
                <TableCell colSpan={10} className="text-center">
                  No Employees
                </TableCell>
              </TableRow>
            )}
          </TableBody>
        </TblContainer>
      </Paper>
    </>
  );
}

export default Employees;
